body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.darktheme {
  background: #59C173;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to left, #5D26C1, #a17fe0, #59C173);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to left, #5D26C1, #a17fe0, #59C173); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
